*, *::before, *::after {
  box-sizing: border-box;
}

/* 2. Remove default margin */
* {
  margin: 0;
}

/* 3. Enable keyword animations */
@media (prefers-reduced-motion: no-preference) {
  html {
    interpolate-size: allow-keywords;
  }
}

body {
  /* 4. Add accessible line-height */
  line-height: 1.5;
  /* 5. Improve text rendering */
  -webkit-font-smoothing: antialiased;
}

/* 6. Improve media defaults */
img, picture, video, canvas, svg {
  display: block;
  max-width: 100%;
}

/* 7. Inherit fonts for form controls */
input, button, textarea, select {
  font: inherit;
}

/* 8. Avoid text overflows */
p, h1, h2, h3, h4, h5, h6 {
  overflow-wrap: break-word;
}

/* 9. Improve line wrapping */
p {
  text-wrap: pretty;
}
h1, h2, h3, h4, h5, h6 {
  text-wrap: balance;
}

/*
  10. Create a root stacking context
*/
#root, #__next {
  isolation: isolate;
}

:root {
    --primary-colour: rgb(80, 57, 57);
    --secondary-colour: rgb(255, 255, 255);
    --tertiary-colour: black;
    font-family: system-ui, -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, Oxygen, Ubuntu, Cantarell, 'Open Sans', 'Helvetica Neue', sans-serif;
}

nav {
    display: flex;
    justify-content: center;
    background-image: linear-gradient(270deg, rgb(63, 40, 40), var(--primary-colour) 70%, rgb(63, 40, 40));
    min-height: 7vh;
    gap: 100px;
}

nav button {
    background-color: transparent;
    border: none;
    color: var(--secondary-colour);
    font-size: 1.5rem;
    font-weight: 600;
}

nav button:hover {
    cursor: pointer;
}

.active-button {
    text-decoration: underline;
}

#content {
    background-image: linear-gradient(rgba(0, 0, 0, 0.3), rgba(0, 0, 0, 0.8)), url('https://images.pexels.com/photos/230484/pexels-photo-230484.jpeg?auto=compress&cs=tinysrgb&w=1260&h=750&dpr=1');
    background-size: cover;
    height: 93vh;
    background-position: center;
    color: var(--secondary-colour);
}

#content::after {
    opacity: 0.1;
}

#home {
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    height: 100%;
}

#home h1 {
    font-size: 13rem;
    font-family: 'Birthstone Bounce', handwriting;
    margin-bottom: -20px;
}

#home p {
    width: 50%;
    text-wrap: balance;
    text-align: center;
    font-style: italic;
    font-size: 1.2rem;
}

#home p:first-of-type {
    margin-bottom: 20px;
}

#menu {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-auto-rows: 500px;
    height: 100%;
    gap: 100px;
    padding: 100px;
    overflow: scroll;
}

.menu-item {
    color: var(--secondary-colour);
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-items: center;
    text-align: center;
    padding: 20px;
    font-size: 1.5rem;
    font-style: italic;
}

.menu-item img {
    width: 100%;
    height: 100%;
    object-fit: cover;
    object-position: center;
    margin-bottom: 10px;
    border-radius: 20px;
    border: 2px solid var(--secondary-colour);
}

#contact {
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100%;
}

#contact div {
  max-width: 30%;
  font-size: 1.5rem;
  font-style: italic;
  text-align: center;
  text-wrap: balance;
}

#contact div p:first-child{
  margin-bottom: 20px;
}

#contact img {
  border-radius: 10px;
}
